html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Lato", sans-serif;
  background-color: #f8f8f8;
  color: #000000;
  font-size: 1rem;
  line-height: 1.618;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

a {
  text-decoration: none;
  color: #000000;
}

.container {
  width: calc(100% - 2rem);
  margin: 0 auto;
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}
@media (min-width: 1400px) {
  .container {
    max-width: 1320px;
  }
}
.section {
  padding: 3rem 0;
}
@media (min-width: 992px) {
  .section {
    padding: 6rem 0;
  }
}

h1, h2, h3 {
  font-weight: 700;
  line-height: 1.2;
  margin: 0 0 1rem;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 1.75rem;
}

h3 {
  font-size: 1.25rem;
}

@media (min-width: 768px) {
  h1 {
    font-size: 2.75rem;
  }

  h2 {
    font-size: 2.25rem;
  }

  h3 {
    font-size: 1.75rem;
  }
}
@media (min-width: 1200px) {
  h1 {
    font-size: 3.25rem;
  }

  h2 {
    font-size: 2.75rem;
  }

  h3 {
    font-size: 2.25rem;
  }
}
p {
  margin: 0 0 1rem;
}

.header {
  padding: 3.5rem 0;
}
.header__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__grid {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  margin-bottom: 3.5rem;
}
@media (min-width: 768px) {
  .header__logo {
    margin-bottom: 0;
  }
}

.navbar {
  background-color: #174743;
  padding: 0.5rem 0;
}
.navbar__border {
  padding: 1rem 0;
  border-top: 2px dashed #ffffff;
  border-bottom: 2px dashed #ffffff;
}

.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .nav {
    flex-direction: row;
    justify-content: space-evenly;
  }
}
.nav__link {
  color: #ffffff;
  font-size: 1.125rem;
  font-weight: 700;
  margin-bottom: 1rem;
}
.nav__link:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .nav__link {
    margin-bottom: 0;
    font-size: 1.25rem;
  }
}

.social-networks {
  display: flex;
}
.social-networks__link {
  margin-right: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.social-networks__link:last-of-type {
  margin-right: 0;
}
.social-networks__link::before {
  content: "";
  padding: 1.25rem;
  display: inline-block;
}
.social-networks__link[href*="https:facebook.com"]::before {
  background-image: url(../img/icono_facebook.svg);
}
.social-networks__link[href*="https:twitter.com"]::before {
  background-image: url(../img/icono_twitter.svg);
}
.social-networks__link[href*="https:instagram.com"]::before {
  background-image: url(../img/icono_instagram.svg);
}
.social-networks__accessibility {
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
  display: block;
}

.welcome {
  text-align: center;
}
.welcome__heading {
  text-align: center;
  color: #994a6a;
  font-family: "Italianno", sans-serif;
  line-height: 1;
  font-weight: 400;
  text-transform: lowercase;
  margin-bottom: 2rem;
  margin-bottom: 2rem;
}
.welcome__heading span {
  color: #000000;
  letter-spacing: 2px;
  text-transform: uppercase;
  display: block;
  font-family: "Lato", sans-serif;
  font-weight: 700;
}
.welcome__text {
  font-size: 1.25rem;
  color: #4b4b4b;
}
@media (min-width: 768px) {
  .welcome__text {
    font-size: 1.5rem;
    max-width: 400px;
    margin: 0 auto;
  }
}

.cards {
  padding-bottom: 3.5rem;
}
.cards__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2px;
}
@media (min-width: 576px) {
  .cards__grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }
}
@media (min-width: 992px) {
  .cards__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

.card {
  width: 100%;
  max-width: 320px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .card {
    max-width: unset;
  }
}
.card__img {
  border: 5px solid #ffffff;
  width: 100%;
}
.card__content {
  background-color: #ffffff;
  max-width: 90%;
  margin: 0 auto;
  transform: translateY(-30px);
  padding-top: 1rem;
}
.card__heading {
  text-align: center;
  color: #994a6a;
  font-family: "Italianno", sans-serif;
  line-height: 1;
  font-weight: 400;
  text-transform: lowercase;
  margin-bottom: 2rem;
}
.card__heading span {
  color: #000000;
  letter-spacing: 2px;
  text-transform: uppercase;
  display: block;
  font-family: "Lato", sans-serif;
  font-weight: 700;
}
.card__link {
  background-color: #994a6a;
  padding: 0.75rem 1.25rem;
  color: #ffffff;
  margin-top: 1rem;
  font-size: 1.25rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
  display: block;
}
@media (min-width: 768px) {
  .card__link {
    width: auto;
  }
}
.card__link:hover {
  background-color: #773952;
}

.schedule {
  background-image: linear-gradient(to bottom, #174743 0%, #174743 50%, transparent 50%), url(../img/bg_horarios.jpg);
  background-repeat: no-repeat;
  background-position: center, bottom center;
  background-size: cover, cover;
  color: #ffffff;
}
@media (min-width: 992px) {
  .schedule {
    background-image: linear-gradient(to right, #174743 0%, #174743 50%, transparent 50%), url(../img/bg_horarios.jpg);
    background-position: center, top center;
    background-size: cover, cover;
  }
}
.schedule__grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
@media (min-width: 992px) {
  .schedule__grid {
    grid-template-rows: unset;
    grid-template-columns: repeat(2, 1fr);
    gap: 3.5rem;
  }
}
.schedule__content {
  padding: 0 0 3.5rem;
}
@media (min-width: 992px) {
  .schedule__content {
    padding: 0;
  }
}
.schedule__heading {
  text-align: center;
  text-transform: uppercase;
  font-weight: 400;
  margin-bottom: 2rem;
}
.schedule__text {
  text-align: center;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .schedule__text {
    font-size: 1.125rem;
    width: 75%;
    margin: 0 auto 2rem;
  }
}
@media (min-width: 992px) {
  .schedule__text {
    width: 85%;
  }
}

.table {
  width: 100%;
  border: 5px solid #994a6a;
  text-align: center;
  border-collapse: collapse;
}
.table__thead {
  background-color: #994a6a;
}
.table__th {
  padding: 1.25rem 0;
  font-size: 1.125rem;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .table__th {
    font-size: 1.25rem;
  }
}
.table__tr:hover {
  background-color: #113431;
}
.table__td {
  padding: 1.25rem 0;
  font-size: 1.125rem;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .table__td {
    font-size: 1.25rem;
  }
}

.products__heading {
  text-align: center;
  color: #994a6a;
  font-family: "Italianno", sans-serif;
  line-height: 1;
  font-weight: 400;
  text-transform: lowercase;
  margin-bottom: 2rem;
  margin-bottom: 3.5rem;
}
.products__heading span {
  color: #000000;
  letter-spacing: 2px;
  text-transform: uppercase;
  display: block;
  font-family: "Lato", sans-serif;
  font-weight: 700;
}
.products__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1.5rem;
}
@media (min-width: 576px) {
  .products__grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 992px) {
  .products__grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1200px) {
  .products__grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

.product__content {
  background-color: #ffffff;
  padding: 1rem;
  text-align: center;
  text-transform: uppercase;
}
.product__title {
  color: #994a6a;
}
.product__description {
  margin-bottom: 1rem;
}
.product__price {
  color: #4b4b4b;
  font-weight: 700;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .product__price {
    font-size: 2.5rem;
  }
}

.text-center {
  text-align: center;
}

.pb-0 {
  padding-bottom: 0;
}